.one-opacity { -webkit-animation:opac 1s; animation:opac 1s }
.four-opacity { -webkit-animation:opac 4s; animation:opac 4s }
  
@-webkit-keyframes opac{from {opacity:0} to{opacity:1}}
  
@keyframes opac{from {opacity:0} to{opacity:1}}

.grow { -webkit-animation-iteration-count: infinite; animation-iteration-count: infinite; -webkit-animation-name: grow; animation-name: grow; }

@-webkit-keyframes grow{
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes grow{
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



.scroll-touch {
  -webkit-overflow-scrolling: touch;
}

