@media (min-width: 1px) and (max-width: 999px) {
  .home-header-tablet-desktop-photogrid {
    display: none;
  }

  .home-header {
    height: calc(100% - 80px);
    padding-top: 80px;
  }

  .page-header-photogrid-mobile {   
    -ms-flex-line-pack: center;   
        align-content: center;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: center;
        justify-content: center;
  }
  
  .photogrid-img {
    -o-object-fit: cover;
       object-fit: cover;
    width: 100%;
    position: relative;
  }
  
  .vertical {    
    opacity: 0.15;
    width: 50%;
    height: calc(50vh - 40px);
  }
  
  .horizontal {
    opacity: 0.15;
    height: calc(50vh - 40px);

  }
  
  .column {
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    height: calc(100vh - 80px);

  }

  .photogrid-div {
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column wrap;
        flex-flow: column wrap;
    top: 40vh;
    width: 100%;
    margin: 0 auto;
  }

  .photogrid-title {
    text-align: center;
    font-size: 75px;
    font-family: "badtaste", sans-serif;
  }

  .photogrid-subheader {
    text-align: center;
    text-transform: uppercase;
    white-space: pre-line;
    line-height: 3;
    font-size: 14px;
    letter-spacing: 0.25em;
    padding: 10px 25px;
    word-spacing: 5px;
  }

  .photogrid-arrow {
    color: rgba(0, 0, 0, 0.8);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
    padding: 50px 0;
  }
}

@media (min-width: 1000px) {
  .home-header {
    height: calc(100% - 135px);
    padding-top: 135px;
  }
  
  .home-header-tablet-desktop-photogrid {
    -ms-flex-line-pack: center;
        align-content: center;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: center;
        justify-content: center;
    height: calc(100vh - 135px);
    }
  
  .photogrid-img {
    -o-object-fit: cover;
       object-fit: cover;
    width: 100%;
    position: relative;
  }
  
  .vertical {
    opacity: 0.15;
    height: calc(100vh - 135px);

  }
  
  .horizontal {
    opacity: 0.15;
    height: calc(50vh - 67.5px);
  }
  
  .column {
    width: 25%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: calc(100vh - 135px);
  }
  
  .photogrid-div {
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column wrap;
        flex-flow: column wrap;
    top: 35vh;
    width: 100%;
    margin: 0 auto;
  }

  .photogrid-title {
    text-align: center;
    font-size: 125px;
    font-family: "badtaste", sans-serif;
  }

  .photogrid-subheader {
    text-align: center;
    text-transform: uppercase;
    font-size: 18px;
    letter-spacing: 0.25em;
    padding: 10px 0;
    word-spacing: 5px;
  }    


  .photogrid-arrow {
    color: rgba(0, 0, 0, 0.8);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 50px;
  }
}




